<template>
  <chart-card title="月度库存">
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 230px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'

export default {
  name: 'YearChartCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'YearChart',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      // const charts = this.$refs[this.refName]
      // charts.showLoading({
      //   text: 'Loading…',
      //   color: '#4ea397',
      //   maskColor: 'rgba(255, 255, 255, 0.4)'
      // })
      this.loading = true
      setTimeout(() => {
        const legendData = ['库存', '入库', '出库', '增减']
        const series = [
          {
            name: '库存',
            type: 'bar',
            data: [
              30073.62,
              32207.71,
              32248.30,
              30608.13,
              27233.58,
              24205.08,
              24909.50,
              23319.82,
              24120.14,
              25762.86,
              25912.14,
              0
            ]
          },
          {
            name: '入库',
            type: 'bar',
            data: [
              2349.89,
              4779.52,
              8188.43,
              5700.05,
              5418.89,
              9404.83,
              6297.58,
              2361.13,
              9770.89,
              11244.36,
              3198.15,
              0
            ]
          },
          {
            name: '出库',
            type: 'bar',
            data: [
              911.94,
              3156.33,
              8621.62,
              7369.72,
              8790.09,
              12905.41,
              6264.23,
              3925.84,
              9490.68,
              10415.15,
              3179.91,
              0
            ]
          }
          // {
          //   name: '增减',
          //   type: 'line',
          //   yAxisIndex: 1,
          //   data: [1, 5, 8, 10, 15, 21, 23, 26, 29, 32, 50, 55]
          // }
        ]
        const xAxis = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
        this.getCompleteOptions(legendData, series, xAxis)
        // charts.hideLoading()
      }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

