
<template>
  <chart-card>
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 190px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import { zoom } from '@/mixins/echarts'
import ChartCard from '@/views/analysis/ChartCard'

export default {
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'Dynamics',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      // const charts = this.$refs[this.refName]
      // charts.showLoading({
      //   text: 'Loading…',
      //   color: '#4ea397',
      //   maskColor: 'rgba(255, 255, 255, 0.4)'
      // })
      this.loading = true
      setTimeout(() => {
        const legendData = []
        const series = [
          {
            type: 'bar',
            barWidth: 16,
            data: [153169144.1, 36682811.03, 31981466.44]
          }
        ]
        const yAxis = ['无动态', '出库数', '入库数']
        this.getCompleteOptions(legendData, series, yAxis)
        // charts.hideLoading()
      }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, yAxis) {
      this.options = {
        title: {
          text: '月度动态',
          left: 0,
          top: 0,
          textStyle: {
            fontSize: '18px',
            fontWeight: 800
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        // legend: {
        //   left: 0
        // },
        grid: {
          top: 25,
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: yAxis
        },
        xAxis: {
          type: 'value',
          axisLabel: {
            formatter: (value, index) => {
              // value大于1000时除以1000并拼接k，小于1000按原格式显示
              if (value >= 1000) {
                value = value / 1000 + 'k'
              } else if (value < 1000) {
                value
              }
              return value
            }
          }
          // boundaryGap: [0, 0.01]
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

