<template>
  <chart-card>
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 340px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import { zoom } from '@/mixins/echarts'
import ChartCard from '@/views/analysis/ChartCard'

export default {
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'StatisticLine',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      // const charts = this.$refs[this.refName]
      // charts.showLoading({
      //   text: 'Loading…',
      //   color: '#4ea397',
      //   maskColor: 'rgba(255, 255, 255, 0.4)'
      // })
      this.loading = true
      setTimeout(() => {
        const legendData = ['一年以内帐龄', '一年到两年帐龄', '两年到三年帐龄', '三年以上帐龄']
        const series = [
          {
            name: '一年以内帐龄',
            type: 'line',
            stack: 'Total',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: [195431554.0453, 210437003.4116, 212940257.2786, 161825765.2332, 220518681.0481, 208976608.8696, 187504217.1748, 169204792.3145, 183001601.3027, 195085282.9888, 193524755.1730, 0]
          },
          {
            name: '一年到两年帐龄',
            type: 'line',
            stack: 'Total',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: [80201235.3522, 85039423.3841, 82176774.6227, 117773137.8176, 36010362.8976, 18323737.7707, 41526747.2184, 44612484.1945, 43347092.9568, 42342677.3498, 41549057.1814, 0]
          },
          {
            name: '两年到三年帐龄',
            type: 'line',
            stack: 'Total',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: [22205393.9469, 22768471.8873, 24689756.5537, 23557246.3975, 12923498.0515, 11427585.0463, 11619042.1679, 15475322.1399, 11692615.5237, 10143014.0596, 9642620.0224, 0]
          },
          {
            name: '三年以上帐龄',
            type: 'line',
            stack: 'Total',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: [114669.1362, 126733.8275, 152675.9339, 681913.3908, 938403.4528, 1438312.104, 1973027.779, 2336598.111, 1922458.4677, 2326632.0027, 2174515.483, 0]
            // data: [105318.4351, 116776.1894, 142821.8886, 677652.8023, 934142.8643, 1409279.2765, 1943994.9514, 2307565.2834, 1922458.4677, 2326632.0027, 2171157.1932, 0]
          }
        ]
        const xAxis = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
        this.getCompleteOptions(legendData, series, xAxis)
        // charts.hideLoading()
      }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

