<template>
  <chart-card>
    <template>
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 190px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'

export default {
  name: 'InventoryCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'Inventory',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      // const charts = this.$refs[this.refName]
      // charts.showLoading({
      //   text: 'Loading…',
      //   color: '#4ea397',
      //   maskColor: 'rgba(255, 255, 255, 0.4)'
      // })
      this.loading = true
      setTimeout(() => {
        // const legendData = ['库存', '入库', '出库', '增减']
        const series = [
          {
            type: 'pie',
            // center: ['53%', '55%'], // 饼图距离容器上下左右边界的调整使用grid调整无效,第一个数值表示左右位置，第二个数值表示上下位置
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: '#fff',
              borderWidth: 3
            },
            data: [
              { value: 10060814.44, name: '0001' },
              { value: 28048402.67, name: '0002' },
              { value: 42259327.08, name: '0003' },
              { value: 914107.25, name: '0004' },
              { value: 50585.18, name: '0005' },
              { value: 50115552.86, name: '0006' },
              { value: 5103762.60, name: '0007' },
              { value: 6851188.87, name: '0008' },
              { value: 946745.99, name: '0009' },
              { value: 0, name: '0010' },
              { value: 77896969.93, name: '0011' },
              { value: 6506243.94, name: '0012' },
              { value: 4766609.52, name: '0013' },
              { value: 25590236.98, name: '0014' }
            ],
            emphasis: {
              label: {
                show: true,
                fontSize: 12
              }
            }
          }
        ]
        // const xAxis = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
        this.getCompleteOptions(null, series, null)
        // charts.hideLoading()
      }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        title: {
          text: '库存占比',
          left: 0,
          top: 0,
          textStyle: {
            fontSize: '18px',
            fontWeight: 800
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

