<template>
  <chart-card title="库存周转率">
    <template>
      <!-- <v-chart :ref="refName" :height="100" :options="options" theme="macarons" :autoresize="true" /> -->
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 230px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'

export default {
  name: 'RatioCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'Ratio',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      setTimeout(() => {
        const legendData = ['期初库存', '期末库存', '出库', '周转率']
        const series = [
          {
            name: '期初库存',
            type: 'bar',

            data: [9044082.86, 27321671.82, 88884474.52, 0.00, 49104.44, 18937842.20, 4620511.67, 5647255.31, 2767837.84, 0.00, 126999466.53, 0.00, 0.00, 1990433.32]
          },
          {
            name: '期末库存',
            type: 'bar',
            data: [10029920.85, 27983393.29, 42365133.46, 914107.25, 50585.18, 50126225.57, 5103762.60, 6851188.83, 946745.92, 0.00, 77956308.64, 6437223.09, 4766609.52, 25590236.98]
          },
          {
            name: '出库',
            type: 'bar',
            data: [87466101.48, 40209546.03, 40438539.92, 76398129.54, 50570741.89, 105314652.06, 26468033.01, 23424337.15, 1778826.93, 20556970.72, 77370460.51, 14895.77, 15009289.79, 190229741.96]
          },
          {
            name: '周转率',
            type: 'line',
            yAxisIndex: 1,
            data: [9.171236706, 1.454099944, 0.616208163, 167.153536, 1014.563841, 3.049766846, 5.443703515, 3.748360498, 0.957753032, 0, 0.754996637, 0.004628011, 6.297679609, 13.79442486]
          }
        ]
        const xAxis = ['0001', '0002', '0003', '0004', '0005', '0006', '0007', '0008', '0009', '0010', '0011', '0012', '0013', '0014']
        this.getCompleteOptions(legendData, series, xAxis)
      }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 4 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

